body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bio-component-container {
    /* background: blue; */
    height: 100%;
    width: 70%;
    margin: auto;
    display: block;
    overflow-x: hidden;
    overflow: hidden;
    
    margin-top: 5vh;
}

.banner-title{
    margin-top: 5vh;
    /* background-color: blue; */
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    /* height: 50%; */
}

.text {
    margin: auto;
    font-size:  1.8vh;
    color: black;
    text-align: center;
    padding: 1vh;
}

#bio-component {
    /* margin-top: 10vh; */
    display: grid;
    height: 90%;
    width: 60%;
    grid-gap: 20px;
    margin: auto;
    margin-top: 5vh;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(1, auto);
    
    grid-template-areas: 
    "pc1  . "
    "pc1  . "
    "pc1 tb1"
    "pc1 tb1"
    "pc1  . " 
    "pc1  . "
    " .   . "
    " .   . ";
}

@media screen and (max-width: 1024px) {
    #bio-component {
        /* margin-top: 10vh; */
        display: grid;
        height: 90%;
        width: 100%;
        grid-gap: 20px;
        margin: auto;
        margin-top: 5vh;
        grid-template-columns: repeat(1, 1fr);
        grid-auto-rows: minmax(1, auto);
        grid-template-areas: 
        "pc1"
        "tb1"
    }
}

@media screen and (max-width: 1024px) {

