.horizontal-line {
    border: 1px solid black;
    width: 60%;
}

.title {
    margin-top: 8vh;
    font-size: 2em;
    text-align: center
}

.section {
    display: block;
    padding-bottom: 25px;
    padding-top: 25px;
    position: static;
    left: 0;
    right: 0;
    color: black;
}

