}

.container1 {
    position: relative;
    text-align: center;
    color: white;
    background: #323639;
}
  
.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60%;
    margin: auto;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.textBottom {
    color: white;
    font-size: 5vw;
}


.name {
    width: 90%;
}

.main {
    width: 100%;
    background: #f7f3ed;
    overflow-x: none 
}

.banner {
    background: #323639;
}
.Button {
    background-color: white;
