}
.banner {
    /* width: 100%; */
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 100vh;
    background-color: black;
    opacity: 0.7;
}



.hobbies-container {
    margin: auto;
    width: 70%;
}

@media screen and (max-width: 1024px) {
    .hobbies-container{
